@mixin closk-size($clock-size) {
  width: $clock-size;
  height: $clock-size;
}

@mixin middle-square($middle-point-size) {
  width: $middle-point-size;
  height: $middle-point-size;
}

@mixin middle-position($middle-position) {
  top: $middle-position;
  left: $middle-position;
}

@mixin hands-square($hands-size) {
  width: $hands-size;
  height: $hands-size;
}
